.title {
    font-weight: 200;
    text-transform: uppercase;
    text-align: center;
    font-size: 48px;
}



.ul {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    align-items: center;
    list-style-type: none;
}


.li {  
    font-size: 34px;
    color: #000000;
    border: 2px solid #ccc;
    font-family: monospace;
    padding: 15px 120px;
    text-align: center;
    margin: 30px;
    border-radius: 20px;
}


.link {
    align-self: flex-start ;
    text-decoration: none; 
}


.input {
    padding: 15px 20px;
    font-size: 18px;
    margin: 20px;
    display: flex;
    justify-content: center;
}


.button {
    padding: 10px 70px;
    border-radius: 20px 20px;
    font-size: 18px;
    background-color: rgb(1, 166, 243);
}